#nfd-nextsteps.nfd-nextsteps {

    .nfd-track {
        border: 1px solid #336AD7;
        border-radius: 4px;
        margin-bottom: 1.5rem;
        transition: all .2s ease;

        &:hover {
            background-color: #EAF4FB;
        }

        .nfd-track-header,
        .nfd-track-sections {
            padding: 1rem;
        }

        .nfd-track-title {
            padding: 0 !important;
            font-size: 1rem;
            font-weight: 600;
        }
    }

    .nfd-track-header,
    .nfd-section-header {
        align-items: center;
        cursor: pointer;
        display: flex;
        color: #121212;
        justify-content: space-between;
        transition: background-color .2s ease-in-out;

        .nfd-header-icon {
            transition: color .2s ease-in-out;
        }

        &:hover {
            .nfd-track-title {
                color: #336AD7;
            }

            .nfd-header-icon {
                color: #336AD7
            }
        }
    }

    .nfd-track-header-icon {
        width: 2rem;
        transform: rotate(180deg);
    }

    .nfd-track[open] {
        background-color: #EAF4FB;

        .nfd-track-title {
            color: #336AD7;
        }

        .nfd-track-header-icon {
            transform: rotate(0deg);
        }
    }

    .nfd-section {
        background-color: #ffffff;
        border: 1px solid #D1E1FF;
        border-radius: 1rem;
        margin-bottom: 1rem;
        overflow: hidden;
        position: relative;

        .nfd-section-header,
        .nfd-section-steps {
            padding: 1rem;
        }

        .nfd-section-header {
            gap: 2rem;
        }
    }

    .nfd-section-header h3 {
        display: flex;
        align-items: center;
        gap: 1rem;
        margin: 0 !important;
    }

    .nfd-section-header-icon,
    .nfd-section-header-icon-opened,
    .nfd-section-header-icon-closed {
        width: 1.75rem;
        height: 1.75rem;
    }

    .nfd-section-header-icon {
        .nfd-section-header-icon-opened {
            display: none;
        }

        .nfd-section-header-icon-closed {
            display: inline-block;
        }
    }

    .nfd-section[open] {
        .nfd-section-header-icon {
            .nfd-section-header-icon-opened {
                display: inline-block;
            }

            .nfd-section-header-icon-closed {
                display: none;
            }
        }
    }

    .nfd-progress-bar {
        background-color: #E0E8EED1;
        border: 1px solid #D7D7D7;
        border-radius: 1rem;
        height: 1rem;
        max-width: 50%;
        position: relative;

        .nfd-progress-bar-inner {
            background-color: #336AD7A8;
            border-radius: 1rem;
            height: calc(1rem - 2px);
            z-index: 1;
            transition: all .2s ease;
        }

        .nfd-progress-bar-label {
            position: absolute;
            top: -1px;
            right: 0.75rem;
            color: #234aa7;
            z-index: 2;
            font-size: 11px;
        }

        // empty style
        &.nfd-progress-bar-0 {
            .nfd-progress-bar-label {
                color: #696969;
            }
        }

        // complete style
        &.nfd-progress-bar-100 {
            .nfd-progress-bar-inner {
                background-color: #A0C00F;
            }

            .nfd-progress-bar-label {
                color: #fff;
            }
        }
    }

    .nfd-section-complete {
        align-items: center;
        background-color: rgba(255, 255, 255, .85);
        border-radius: 1rem;
        display: flex;
        flex-direction: column;
        height: calc(100% - 4.25rem);
        justify-content: center;
        left: 0;
        max-height: 100%;
        opacity: 0;
        padding: 0 1rem 1rem;
        position: absolute;
        top: 100%;
        transition: all .15s ease;
        width: 100%;

        &[data-complete="true"] {
            opacity: .95;
            top: 4.25rem;
            z-index: 1;
        }

        .nfd-nextsteps-section-close-button {
            height: 1.75rem;
            position: absolute;
            right: 1rem;
            top: 0.25rem;
            width: 1.75rem;
        }

        .nfd-section-celebrate {
            display: flex;
            color: #A0C00F;
            height: 6rem;
            margin: 0 auto 0;
            max-height: 100%;
            min-height: 2rem;
            width: auto;

            svg path {
                stroke-width: 1px;
            }
        }

        .nfd-section-celebrate-text {
            color: #121212;
            font-size: 1rem;
            font-weight: 600;
            margin: 0 auto 0;
            text-align: center;
        }
    }

    .nfd-nextsteps-step-card {
        border: none;
        background: #F2F5F9;
        border-radius: 1rem;
        padding: 20px;
        transition: .2s all ease-in-out;

        .nfd-nextsteps-step-card__wireframe {
            border-radius: 10px;
            overflow: hidden;
        }

        &:not(.nfd-nextsteps-step-card--wide) .nfd-nextsteps-step-card__wireframe {
            width: 100%;

            svg {
                width  : 100%;
                height : auto;
            }
        }

        &.nfd-nextsteps-step-card--wide .nfd-nextsteps-step-card__wireframe {
            height: 100%;
            min-width: fit-content;

            svg {
                height : 100%;
                width  : auto;
            }
        }

        .nfd-nextsteps-step-title-wrapper {
            display     : flex;
            align-items : center;
            gap         : .7rem;

            .nfd-nextsteps-step-icon-wrapper {
                padding    : 6px;
                background : #ddecff;
                border-radius: 7px;
            }
        }

        .nfd-nextsteps-step-title {
            height: 28px;
            font-size : 1rem;
        }

        &:hover {
            background-color: #EAF4FB;
            border-color: #336AD7;

            .nfd-nextsteps-button-dismiss {
                opacity: 1;
            }
        }
    }

    .nfd-nextsteps-step-card-done {
		align-items      : flex-start;
		background-color : #F5F9EE !important;
		border-color     : #78ae144a;

		.nfd-nextsteps-step-card__wireframe {
			opacity : 50%;
		}

        .nfd-nextsteps-step-title-wrapper {
            opacity : 50%;

            .nfd-nextsteps-step-icon-wrapper {
                background : #e4eed2;
            }
        }

        .nfd-nextstep-step__completed-badge{
            background: #E5F7C6;
            color: #ABAE14;
            padding: 2px 10px 2px 2px;
            display: flex;
            gap: 3px;
            align-items: center;
        }

        &:hover {
            border-color : #abae14;
        }
    }

    .nfd-nextsteps-step-dismissed {
        text-decoration: line-through;
    }

    .nfd-ml-auto {
        margin-left: auto;
    }

    .nfd-nextsteps-button {
        align-items: center;
        display: inline-flex;
        text-decoration: none;
        transition: all .2s ease;

        svg {
            width: 100%;
            height: auto;
        }
    }

    .nfd-nextsteps-button-todo {
        width: 1.5rem;

        &:hover {
            color: #ABAE14;
        }
    }

    .nfd-nextsteps-button-dismiss {
        width: 1rem;
        opacity: 0;
    }

    .nfd-nextsteps-button-link {
        width: 1.25rem;
    }

    .nfd-nextsteps-button-redo {
        width: 1.5rem;
        color: #ABAE14;
    }

}

.nfd-nextstep-tasks-modal {
    p {
        margin-bottom : 1.5rem;
    }

    .nfd-nextsteps-step {
        border        : 1px solid #d7d7d7;
        border-radius : 2rem;
        padding: 8px 13px 8px 8px;
        margin-bottom : 10px;
        transition    : .2s all ease-in-out;

        a {
            text-decoration : none;
        }

        .nfd-nextsteps-step-title {
            height : 28px;
        }

        &:hover {
            background-color : #eaf4fb;
            border-color     : #336ad7;

            .nfd-nextsteps-button-dismiss {
                opacity : 1;
            }
        }
    }

    .nfd-nextsteps-step-done {
        align-items      : flex-start;
        background-color : #F5F9EE !important;
        border-color     : #78ae144a;

        .nfd-nextsteps-step-title {
            color: #696969;
            font-size: .875rem;
        }

        &:hover {
            border-color : #abae14;
        }
    }

    .nfd-nextsteps-step-dismissed {
        text-decoration: line-through;
    }

    .nfd-ml-auto {
        margin-left: auto;
    }

    .nfd-nextsteps-button {
        align-items: center;
        display: inline-flex;
        text-decoration: none;
        transition: all .2s ease;

        svg {
            width: 100%;
            height: auto;
        }
    }

    .nfd-nextsteps-button-todo {
        width: 1.5rem;

        &:hover {
            color: #ABAE14;
        }
    }

    .nfd-nextsteps-button-dismiss {
        width: 1rem;
        opacity: 0;
    }

    .nfd-nextsteps-button-link {
        width: 1.25rem;
    }

    .nfd-nextsteps-button-redo {
        width: 1.5rem;
        color: #ABAE14;
    }
}

// # Task modal

.nfd-nextstep-tasks-modal {
    h1.nfd-title {
        font-size : 1.25rem !important;
    }
}
